import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'speler',
  templateUrl: './speler.component.html',
  styleUrls: ['./speler.component.scss']
})
export class SpelerComponent {

  @Input('naam') naam: string;

  @Output('verwijderd') verwijderd = new EventEmitter() ;

  
  @Input ('rondeScores') rondeScores: {
    ronde: number,
    score: number,
    gezegd: number,
    gehaald: number
  }[] = [];

  constructor() { }

  verwijderen(){
    this.verwijderd.emit(this.naam);
    
  }


}



