import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { AppComponent } from './app.component';
import { SpelerComponent } from './components/speler/speler.component';
import { BoerenbridgeComponent } from './components/boerenbridge/boerenbridge.component';
import { NieuweSpelerComponent } from './components/nieuwe-speler/nieuwe-speler.component';

@NgModule({
  declarations: [
    AppComponent,
    SpelerComponent,
    BoerenbridgeComponent,
    NieuweSpelerComponent
  ],
  imports: [
    BrowserModule,
    FormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



