import { Component } from '@angular/core';

import { SpelerComponent } from '../speler/speler.component';

@Component({
  selector: 'app-boerenbridge',
  templateUrl: './boerenbridge.component.html',
  styleUrls: ['./boerenbridge.component.scss']
})
export class BoerenbridgeComponent {



  ronde = 0;
  rondeWeergave = 0;
  vorigeRonde = 0;
  totaalAantalGehaald = 0;
  spelers: SpelerComponent[];
  deler = '';
  spelFase = '';
  aanDeBeurt = 0;
  aantalGeweest = 0;
  slagenGezegd: number;
  slagenGehaald: number;
  waarschuwing: string;
  nietZeggen: number;
  scoreVolgorde: {naam: string, score: number, kleur: any}[] = [];

  constructor() {
    this.spelers = [];
  }


  nieuweSpeler(spelerNaam: string){

      if (this.spelers.length < 5){
        this.scoreVolgorde.push({naam: spelerNaam, score: 0, kleur: 'black'});
        this.spelers.push( new SpelerComponent() ) ;
        this.spelers[this.spelers.length - 1].naam = spelerNaam;


      } else
        { window.alert('Te weinig kaarten voor meer dan 5 spelers!'); }

  }

  spelerVerwijderd(spelerNaam: string){
    const teVerwijderen = this.spelers.find(item => item.naam == spelerNaam);
    const i = this.spelers.indexOf(teVerwijderen);
    this.spelers.splice(i, 1);

  }

  starten(){
    this.ronde = 1;
    this.rondeWeergave = 1;
    this.spelers.forEach(element => {
      element.rondeScores.push({ronde: 0, score: 0, gehaald: 0, gezegd: 0});

    });

    const rand = this.spelers[Math.floor(Math.random() * this.spelers.length)];
    this.deler = rand.naam;
    this.volgendeSpeler(rand);
    this.spelFase = 'zeggen';

  }

  kiesKleuren(){
    if (this.spelers.length == 2){
      this.scoreVolgorde[0].kleur = 'limegreen';
      this.scoreVolgorde[1].kleur = 'red';
    }
    else if (this.spelers.length == 3){
      this.scoreVolgorde[0].kleur = 'limegreen';
      this.scoreVolgorde[1].kleur = 'gold';
      this.scoreVolgorde[2].kleur = 'red';
    }
    else if (this.spelers.length == 4){
      this.scoreVolgorde[0].kleur = 'limegreen';
      this.scoreVolgorde[1].kleur = 'gold';
      this.scoreVolgorde[2].kleur = 'orange';
      this.scoreVolgorde[3].kleur = 'red';
    }

    else if (this.spelers.length == 5){
      this.scoreVolgorde[0].kleur = 'limegreen';
      this.scoreVolgorde[1].kleur = 'greenyellow';
      this.scoreVolgorde[2].kleur = 'gold';
      this.scoreVolgorde[3].kleur = 'orange';
      this.scoreVolgorde[4].kleur = 'red';
    }
  }

  volgendeSpeler(huidigeSpeler){

    if (this.spelers.indexOf(huidigeSpeler) + 1 == this.spelers.length)
      {this.aanDeBeurt = 0; }
    else { this.aanDeBeurt = this.spelers.indexOf(huidigeSpeler) + 1; }

  }

  slagenZeggen(){
    if (this.slagenGezegd > this.rondeWeergave || this.slagenGezegd < 0){this.waarschuwing = 'Dat ga je echt niet halen!'; }

    else if (this.aantalGeweest <  this.spelers.length - 1){
      this.slagenInvullen();
      this.aantalGeweest ++;
      this.waarschuwen();


    }
    else{
      if (this.nietZeggen == this.slagenGezegd){

      } else{
        this.slagenInvullen();
       this.aantalGeweest = 0;
        this.waarschuwing = null;

       this.spelFase = 'halen';

           setTimeout(function(){
      window.alert('Speel de ronde en kom terug als je klaar bent!');
    }, 100);

      }
    }

  }

  slagenInvullen(){
    this.spelers[this.aanDeBeurt].rondeScores.push({ ronde:  this.rondeWeergave, score: null, gezegd: this.slagenGezegd, gehaald: null });
    this.slagenGezegd = null;
    this.volgendeSpeler(this.spelers[this.aanDeBeurt]);

  }

  waarschuwen(){

    if (this.aantalGeweest ==  this.spelers.length - 1 ){
      let totaal = 0;

      this.spelers.forEach(element => {

        if (element.rondeScores[this.ronde]){
          totaal += +element.rondeScores[this.ronde].gezegd; }
      });

      this.nietZeggen = this.rondeWeergave - totaal;
        if (this.nietZeggen >= 0){ this.waarschuwing = 'Je mag geen ' + this.nietZeggen + ' zeggen!'; }
    } else {this.waarschuwing = null; }

  }

    slagenHalen(){
    if (this.slagenGehaald > this.rondeWeergave || this.slagenGezegd < 0){this.waarschuwing = 'Weet je dat heel zeker?'; }
    if (this.totaalAantalGehaald + +this.slagenGehaald > this.rondeWeergave){this.waarschuwing = 'Zoveel kaarten zijn er niet uitgedeeld...'; }

    else if (this.aantalGeweest <  this.spelers.length - 1){

      this.scoreBerekenen();
      this.aantalGeweest ++;
      this.voorInvullen();
    }
    else{
        if ((this.totaalAantalGehaald + +this.slagenGehaald) < this.rondeWeergave){this.waarschuwing = 'Zo kom je wat slagen te kort..'; }
        else{
        this.scoreBerekenen();
        this.aantalGeweest = 0;
        this.totaalAantalGehaald = 0;
        this.waarschuwing = null;
        this.volgendeRonde();
        this.volgendeDeler();
        this.volgendeSpeler(this.spelers[this.aanDeBeurt]);
        this.maakVolgorde();
        this.spelFase = 'zeggen';
        }
     }
    }

    voorInvullen(){

    if (this.aantalGeweest ==  this.spelers.length - 1 ){
      let totaal = 0;

      this.spelers.forEach(element => {

        if (element.rondeScores[this.ronde]){
          totaal += +element.rondeScores[this.ronde].gehaald; }
      });

      this.slagenGehaald = this.rondeWeergave - totaal;
      }

    }


    scoreBerekenen(){
    this.totaalAantalGehaald = +this.totaalAantalGehaald + +this.slagenGehaald;
    this.waarschuwing = null;
    let gehaaldeScore: number;
    const gezegd: number = +this.spelers[this.aanDeBeurt].rondeScores[this.ronde].gezegd;

      if ((this.ronde == 10 || this.ronde == 11 || this.ronde == 12)
        && (gezegd == this.slagenGehaald && gezegd == 0)){gehaaldeScore = +20; }
    else
    {
      if ( gezegd == this.slagenGehaald){gehaaldeScore =  (10 + +this.slagenGehaald); }
    else {gehaaldeScore = 0 - Math.abs(gezegd - +this.slagenGehaald); }
    }

     this.spelers[this.aanDeBeurt].rondeScores[this.ronde].score =  this.spelers[this.aanDeBeurt].rondeScores[this.ronde - 1].score + gehaaldeScore;
     this.spelers[this.aanDeBeurt].rondeScores[this.ronde].gehaald = this.slagenGehaald;

     this.scoreVolgorde.forEach(element => {

        if (element.naam == this.spelers[this.aanDeBeurt].naam){
          element.score = this.spelers[this.aanDeBeurt].rondeScores[this.ronde].score; }
     });

     this.slagenGehaald = null;
     this.volgendeSpeler(this.spelers[this.aanDeBeurt]);
   }

   volgendeDeler(){
     const huidigeDelerI = this.spelers.indexOf(this.spelers.find( item => item.naam == this.deler));

         if (huidigeDelerI + 1 == this.spelers.length)      {this.deler = this.spelers[0].naam; }
    else { this.deler = this.spelers[+huidigeDelerI + 1].naam; }

   }

   volgendeRonde(){

     if (this.ronde < 10) {this.rondeWeergave++; this.ronde++; }
     else if (this.ronde == 10 || this.ronde == 11) {this.ronde++; }
     else if (this.ronde > 11 && this.ronde < 22){this.rondeWeergave--; this.ronde++; }
     else {this.spelFase = 'klaar'; }
   }

   maakVolgorde(){

   this.scoreVolgorde.sort(function(a, b){return (b.score - a.score); });
   this.kiesKleuren();
   console.log(this.scoreVolgorde);


   }

   findColor(naam: string){

     let styles = {   'color': 'pink'  };


      this.scoreVolgorde.forEach( element => {
        if (naam == element.naam){ styles = {   'color': element.kleur  }; }
      });

        return styles;

   }


   slagenOpnieuw(){
     this.aantalGeweest = 0;

      this.spelers.forEach(element => {

        if (element.rondeScores[this.ronde]){
          element.rondeScores.splice(this.ronde, 1); }
      });


      const huidigeDelerI = this.spelers.indexOf(this.spelers.find( item => item.naam == this.deler));
     if (huidigeDelerI + 1 == this.spelers.length)      {this.aanDeBeurt = 0; }
    else { this.aanDeBeurt = huidigeDelerI + 1; }

    this.waarschuwing = null;
    this.slagenGezegd = null;

   }

     gehaaldOpnieuw(){
     this.aantalGeweest = 0;

      this.spelers.forEach(element => {

        if (element.rondeScores[this.ronde]){
          element.rondeScores[this.ronde].gehaald = null; }
      });


      const huidigeDelerI = this.spelers.indexOf(this.spelers.find( item => item.naam == this.deler));
     if (huidigeDelerI + 1 == this.spelers.length)      {this.aanDeBeurt = 0; }
    else { this.aanDeBeurt = huidigeDelerI + 1; }

    this.waarschuwing = null;
    this.slagenGehaald = null;

   }

   rondeOpnieuw(){

   this.aantalGeweest = 0;

      this.spelers.forEach(element => {

        if (element.rondeScores[this.ronde]){
          element.rondeScores.splice(this.ronde, 1); }
      });


      const huidigeDelerI = this.spelers.indexOf(this.spelers.find( item => item.naam == this.deler));
     if (huidigeDelerI + 1 == this.spelers.length)      {this.aanDeBeurt = 0; }
    else { this.aanDeBeurt = huidigeDelerI + 1; }

    this.waarschuwing = null;
    this.slagenGezegd = null;

    this.spelFase = 'zeggen';


   }



}



