import { Component, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'nieuwe-speler',
  templateUrl: './nieuwe-speler.component.html',
  styleUrls: ['./nieuwe-speler.component.scss']
})
export class NieuweSpelerComponent {

  @Output('nieuw') nieuw = new EventEmitter();

  gegevenNaam: string = "";

  emit(){

    this.nieuw.emit(this.gegevenNaam);
    this.gegevenNaam = "";
  }

}



